header
{
    /*border: 3px solid green;*/
    background-color: darkorange;
    padding: 0px;
    margin: 0px;
    overflow: hidden; /* this causes the header to assume the height of its children*/
}
header h1:first-of-type
{
    float: left;
    width: 70%;
    text-align: left;
    line-height: 1;
    margin: 10px;
    font-size: 20pt;
    font-style: italic;
    font-weight: normal;
}
header h1:last-of-type {
    float: right;
    margin: 2px;
}
header h1 input
{
    background-color: #ff9c08;
    margin: 0px 10px;
    border: 1px solid black;
    border-radius: 8px;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    -ms-transform: translateY(-50%);
}
nav
{
    clear:both;
    background: darkorange;
}
nav ul
{
    margin: 0px 0px 0px 0px;
    padding: 0px;
    list-style-type: none;
    overflow: hidden;
    width: 72em;
}
nav ul li
{
    float: left;
    background-color: #ff9c08;
    margin: 0px 0px 0px 0px;
    border-top: 1px solid black;
    border-right: 1px solid rgb(0,0,0);
    border-top-right-radius: 8px;
    border-left: 1px solid black;
    border-top-left-radius: 8px;
}
/* this style gets assigned to the highlighted tab classList and overrides the background-color.  Hover still works though */
.tabhighlighted
{
    background-color: #ffac28;
}
/* gets added as you hover */
nav ul li:hover
{
    background-color: #ffbc48;
}
nav ul li:first-child
{
    margin: 0px 0px 0px 0.5em;
}
nav * a
{
    padding: 0.2em;
    line-height: 2.1em;
    text-decoration: none;
    color: #000;
}
footer
{
    background-color: darkorange;
    clear: both;
    font-size: 15pt;
}
/* Style the article section */
body
{
    background-color: black;
}
.wrapper
{
    clear: both;
    width: 100%;
}
.wrapleft
{
    float:left;
    width:100%;
}
article
{
    height: 800px;
    overflow: auto;
    margin-right: 310px;
}
article table
{
    width: 100%;
    border-collapse:collapse;
}
article table tr
{
    background-color: #000000;
    color: darkorange;
}
article table tr td
{
    border: 2px solid darkorange;
}
article table td:nth-child(1)
{
    width: auto;
}
article table td:nth-child(2)
{
    width: 20%;
}
article table td:nth-child(3)
{
    width: 20%;
}
article table td:nth-child(4)
{
    width: 40%;
}
article table td:nth-child(5)
{
    width: 20%;
}
/* Style the aside */
aside
{
    float: Right;
    width: 300px;
    margin-left: -300px;
}
aside table
{
    width: 100%;
    border-collapse: collapse;
}
aside table tr
{
    background-color: #000000;
    color: darkorange;
}
article table tr td
{
    border: 2px solid darkorange;
}
aside tr td:first-of-type
{
    width: 40%;
}
aside tr td:last-of-type
{
    width: 60%;
    font-style: italic;
}
/* EDSM Buttons on aside */
.edsmetcbuttons
{
    width: 6em;
    margin: 1px 0px 1px 10px;
    line-height: 1;
    background-color: orange;
    border: 1px solid darkorange;
    color: #000;
    text-decoration: none;
}
footer button
{
    width: 6em;
    color: orange;
    background-color: black;
    border: 1px solid darkorange;
}
/* Chrome only */
::-webkit-scrollbar
{
    width: auto;
}
::-webkit-scrollbar-track
{
    background: darkorange;
}
::-webkit-scrollbar-thumb
{
    background: rgb(80,80,80);
}
::-webkit-scrollbar-thumb:hover
{
    background: rgb(100,100,100);
}
body p 
{
    color: aliceblue;
}
/* Style a item div wrapped in tooltip */
.tooltip
{
    display: inline;
}
/* Tooltip text - you should position it to your satisfaction */
.tooltip .tooltiptext
{
    visibility: hidden;
    color: orange;
    background-color: black;
    border: 1px solid orange;
    border-radius: 1px;
    text-align: center;
    padding: 5px 0;
    position: absolute;
}
/* Show the tooltip text when you mouse over the tooltip container */
.tooltip:hover .tooltiptext
{
    -webkit-transition-delay: 500ms;
            transition-delay: 500ms;
    -webkit-transition-duration: 2000ms;
            transition-duration: 2000ms;
    visibility: visible;
}






























































